import { useEffect, useRef, useState } from "react";
import { matchesLabel, matchesPhase } from "../../scripts/id-util";
import SearchResults from './SearchResults';

const Contraindications = ({ self: solution, display }) => {
    if (!solution.contraindications) solution.contraindications = [];

    const [num, setNum] = useState(0);

    useEffect(() => {
        setNum(solution.contraindications.length);
    }, [num, solution.contraindications.length]);

    return (
        <div className={`contraindications ${display ? "" : "hidden"}`}>
            <p className="mdb-block-heading">Contraindications:</p>
            <div className="column-headers">
                <p>ID</p>
                <p>Phase</p>
                <p>Why</p>
            </div>
            {[...Array(num)].map((_, i) => (
                <ContraindicatedItem  key={`contraindication=${i}`} self={solution} num={i} numState={[num, setNum]} />
            ))}
            <button type="button" onClick={() => setNum(num + 1)}>New Contraindication</button>
        </div>
    );
}

export default Contraindications;

const ContraindicatedItem = ({ self: solution, num, numState }) => {
    if (!solution.contraindications[num]) 
        solution.contraindications[num] = { id: "", phase: "", why: "" };

    const self = solution.contraindications[num];
    const [, setNum] = numState;

    const updateSelf = (e) => {
        self[e.target.name] = e.target.value;
    }

    const deleteSelf = () => {
        solution.contraindications.splice(num, 1);
        setNum(solution.contraindications.length);
    }

    return (
        <div className="inputs">
            <Searchable 
                placeholder="ID"
                name={"id"}
                matchFunction={matchesLabel}
                self={self}
                />
            <Searchable 
                placeholder="Phase"
                name={"phase"}
                matchFunction={matchesPhase}
                self={self}
                />
            <textarea 
                placeholder="Why"
                name="why"
                defaultValue={self.why}
                onChange={updateSelf} />
            <button type="button" onClick={deleteSelf}>-</button>
        </div>
    );
}

const Searchable = ({ label, placeholder, name, matchFunction, self }) => {
    const [query, setQuery] = useState();
    const idRef = useRef();

    const setValue = (e) => { 
        idRef.current.value = e.target.dataset.label; 
        self[`${name}-label`] = e.target.dataset.label;
        self[name] = e.target.dataset.id
        setQuery(null); 
    }

    return (
        <label className="searchable">
            {!!label && label}
            <input 
                placeholder={placeholder}
                onChange={e => setQuery(e.target.value)}
                defaultValue={self[`${name}-label`]}
                name={name}
                type={"text"} 
                ref={idRef}
            />
            <SearchResults list={matchFunction(query)} setVal={setValue} />
        </label>
    )
};