import { useState } from "react";

export default function Indications({ med, update }) {
    const [expanded, setExpanded] = useState(false);

    const contraindications = [
        "Analgesic",
        "Sedated Intubation ",
        "Hypotension",
        "Sedation",
        "Nausea/vomiting",
    ];

    const changeView = () => setExpanded(e => !e);

    return (
        <div className="indications">
            <div className="dropdown" onClick={changeView}>Indications </div>
            <div className={`checklist ${!expanded ? "hidden" : ""}`}>
                {contraindications.map((c, idx) => (
                    <div key={c} className="indication">
                        <label>
                            <input 
                                type="checkbox" 
                                onChange={update} 
                                data-type={"generic"}
                                data-index={idx}
                                data-key="indications"
                                name={c}
                                defaultChecked={med.indications.includes(c)}
                            />
                            {c}
                        </label>
                    </div>
                ))}
            </div>
        </div>
    )
}