if ("pam" === process.env.REACT_APP_APP_ENV) {
    console.log("using pjordan config")
    userApp = getOrInitializeApp(pamFirebaseCfg, "pam")
} else if ("scott" === process.env.REACT_APP_APP_ENV) {
    console.log("using scott config")
    userApp = getOrInitializeApp(scottFirebaseConfig)
}
else {
    console.log("using production config")
    userApp = getOrInitializeApp(prodFirebaseCfg, "production")
}

