import React, { useState } from 'react';
import { useDebug } from '../../scripts/custom-hooks';

import Problem from './Problem';

const FormRoot = ({ scenario }) => {
    if (!scenario.problems) {// Backwards compatability
        scenario.problems = [{}];
    }

    const [numProblems, setNumProblems] = useState(scenario.problems.length);

    useDebug(() => console.log(scenario));

    const addProblem = () => {
        setNumProblems(numProblems + 1);
        scenario.problems.push({});
    }

    if (!scenario) return <p>Loading...</p>;

    return (
        <div className="mdb-form-wrapper">
            <form id="debriefer-form" className="debriefer-form" autoComplete="off" >

                {[...Array(numProblems)].map( (_, i) => (
                    <Problem key={`block-${i}`} problems={scenario.problems} number={i} />
                ))}

                <button type="button" onClick={addProblem}>New Problem</button>
            </form>
        </div>
    );

}

export default FormRoot;