import { MDB_TYPES as MDB } from '@meddbriefer/scenario-data/constants.js';


let phases = [];
let assessments = [];
let intvIdList = []

let vitals = [
    { label: "Blood pressure", id: "BP" },
    { label: "Pulse", id: "P" },
    { label: "Respirations", id: "R" },
    { label: "Skin condition", id: "Skin" },
    { label: "Sp02", id: "Sp02" },
    { label: "Etc02", id: "ETC02" },
    { label: "GCS", id: "GCS" },
    { label: "Glucose", id: "Glucose" },
    { label: "Pain", id: "Pain" },
    { label: "Temperature", id: "Temp" },
];

const globalsAreInit = () => {
    if ((phases.length === 0) ||
        (assessments.length === 0) ||
        (intvIdList.length === 0)) {
        return false
    }
    return true
}

const initGlobals = (scenTypeData) => {
    intvIdList = scenTypeData._interventions.map(rec => {
                    return {id: rec.id, label: rec.label, type: MDB.INTERV}
                })
                .sort((one, two) => {
                    if (one.label.localeCompare(two.label) < 0) return -1
                    if (one.label.localeCompare(two.label) > 0) return 1
                    return 0;
                })
        assessments = scenTypeData._checklist.filter(e => e.type === MDB.ASSESS)
        phases = scenTypeData._checklist.filter(item => item.type === MDB.PHASE || item.type === MDB.SUB_PHASE);
}

export const getLabel = (scenType, id, type) => {
    if (!globalsAreInit()) {
        initGlobals(scenType)
    }
    switch (type) {
        case "assessments":
            return assessments.find(e => e.id === id);
        case "phases":
            return phases.find(e => e.id === id);
        case "vitals":
            return vitals.find(e => e.id === id);
        case "interventions":
            return intvIdList.find(e => e.id === id);
        default:
            return null;
    }
}

export const matchesVital = (str) => {
    if (!str || str === "") return null;
    let matchArr = [];

    for (let vital of vitals)
        if (vital.label.toLowerCase().startsWith(str.toLowerCase()))
            matchArr.push(vital);

    return matchArr.length > 0 ? matchArr : null;
};

export const matchesPhase = (scenType, str) => {
    if (!globalsAreInit()) {
        initGlobals(scenType)
    }
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of phases)
        if (item.label?.toLowerCase().includes(str.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

export const matchesLabel = (scenType, str) => {
    if (!globalsAreInit()) {
        initGlobals(scenType)
    }
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of intvIdList)
        if (item.label?.toLowerCase().includes(str.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}

