export const matchesChecklist = (scenType, str) => {
    if (!globalsAreInit()) {
        initGlobals(scenType)
    }
    if (!str || str === "") return null;
    let matchArr = [];

    for (let item of assessments)
        if (item.label?.toLowerCase().includes(str.toLowerCase()))
            matchArr.push(item);

    return matchArr.length > 0 ? matchArr : null;
}