import { useEffect, useState } from "react";

import { db } from "@meddbriefer/mdb-firebase";

import { getScenarioType } from "@meddbriefer/scenario-data/index.js";

import blankTrauma from '../../data/blank-trauma.json';

import { updateSchema } from "./traumaScenario.js";


import SetScenarioNameForm from "./SetScenarioNameForm.js";

const NewScenario = () => {
    const [scenario, setScenario] = useState(null)

    useEffect(
        () => {
            (async () => {
                const scenTypeData = await getScenarioType(db, "trauma", "V2")
                setScenario(updateSchema({}, scenTypeData, blankTrauma))
                }
            )()
        }, []
    )

    return (
        <SetScenarioNameForm scenario={scenario} isClone={false} />
    )
}

export default NewScenario