import { useRef, useState, useEffect } from "react";
import { matchesLabel, matchesPhase } from "../../scripts/id-util";
import SearchResults from './SearchResults';
import { useScenarioEditor } from "../ScenarioEditor/ScenarioEditorContext";

const Actions = ({ self: solution, display }) => {
    if (!solution.actions) solution.actions = [];

    const [num, setNum] = useState(0);

    useEffect(() => {
        setNum(solution.actions.length);
    }, [num, solution.actions]);

    return (
        <div className={`actions ${display ? "" : "hidden"}`}>
            <p className="mdb-block-heading">Actions:</p>
            <div className="column-headers">
                <p>ID</p>
                <p>Phase</p>
                <p>Why</p>
                <p>Optional?</p>
                <p>Minimal?</p>
            </div>
            {[...Array(num)].map((_, i) => (
                <ActionItem key={`action-${i}`} self={solution} num={i} actionState={[num, setNum]} />
            ))}
        <button type="button" onClick={() => setNum(num + 1)}>New Action</button>
        </div>
    );
}

export default Actions;

const ActionItem = ({ self: solution, num, actionState }) => {
    const [, setNum] = actionState;

    if (!solution.actions[num])
        solution.actions[num] = { id: "", phase: "", why: "", type: "" };

    const self = solution.actions[num];

    const updateSelf = (e) => {
        if (e.target.type === "text")
            self[e.target.name] = e.target.value;
        else if (e.target.type === "radio")
            self.type = e.target.value;
    }

    const deleteSelf = () => {
        solution.actions.splice(num, 1);
        setNum(solution.actions.length);
    }

    return (
        <div className="inputs">
            <Searchable
                placeholder="ID"
                name={"id"}
                matchFunction={matchesLabel}
                self={self}
                />
            <Searchable
                placeholder="Phase"
                name={"phase"}
                matchFunction={matchesPhase}
                self={self}
            />
            <textarea
                placeholder="Why"
                onChange={updateSelf}
                defaultValue={self.why}
                name="why" />

            <input
                onChange={updateSelf}
                defaultChecked={self.type === "optional"}
                value="optional"
                name={`${num}-type`}
                type="radio" />
            <input
                onChange={updateSelf}
                defaultChecked={self.type === "minimal"}
                value="minimal"
                name={`${num}-type`}
                type="radio" />
        <button type="button" onClick={deleteSelf}>-</button>
        </div>
    );
}

const Searchable = ({ label, placeholder, name, matchFunction, self }) => {
    const [query, setQuery] = useState();
    const idRef = useRef();
    const {scenarioType} = useScenarioEditor()

    const setValue = (e) => {
        idRef.current.value = e.target.dataset.label;
        self[`${name}-label`] = e.target.dataset.label;
        self[name] = e.target.dataset.id
        setQuery(null);
    }

    return (
        <label className="searchable">
            {!!label && label}
            <input
                placeholder={placeholder}
                onChange={e => setQuery(e.target.value)}
                defaultValue={self[`${name}-label`]}
                name={name}
                type={"text"}
                ref={idRef}
            />
            <SearchResults list={matchFunction(scenarioType, query)} setVal={setValue} />
        </label>
    )
};