import classnames from "classnames";
import ProblemsForm from '../Problems/ProblemsForm';
import { useScenarioEditor } from './ScenarioEditorContext';

const Problems = ({ active }) => {
    const { scenario, saveScenario } = useScenarioEditor();

    const saveChanges = async () => {
        // Validate problems, only saving inputs with actual content
        for (const problem of scenario.problems) {
            problem.assessments = problem.assessments.filter(a => a.trim() !== "");
            problem.vitals = problem.vitals.filter(v => v.id.trim() !== "");
            problem.notes = problem.notes.filter(n => n.trim() !== "");

            for (const solution of problem.solutions) {
                solution.actions = solution.actions.filter(a => a.id.trim() !== "");
                solution.contraindications = solution.contraindications.filter(c => c.id.trim() !== "");
            }
        }

        await saveScenario(scenario);
    }

    return (
        <div className={classnames("scenario-form", { "scenario-view-hidden": !active })}>
            <div id="problems" className="info-wrapper">
                <ProblemsForm scenario={scenario} />
                <button type="button" onClick={saveChanges}>Save Changes</button>
            </div>
        </div>
    );
}

export default Problems;