import { MDB_TYPES as TYPES } from "@meddbriefer/scenario-data/constants.js"

const REC_TYPES_WITH_FINDINGS = [TYPES.ASSESS, TYPES.ASSESS_OPTION]
const REC_TYPES_WITH_OPTIONS = [TYPES.AWO, TYPES.DECISION]

const MED_DEFAULTS = {
    initialDosage: "",
    // repeatDosage: "",
    // repeatFrequency: "",
    // maxDosage: "",
    route: "",
    contraindications: [],
}

export const updateSchema = (scen, scenType, tmpl) => {
    const CLMD = scenType._checklist
    const MEDS = scenType._medications
    const INTVS = scenType._interventions
    const INTV_PROMPTS = scenType._interventionPrompts
    const INTV_PROMPT_ANSWERS = scenType._interventionPromptAnswers

    const additions = []
    const deletions = []
    // copies over missing fields from tmpl while throwing away
    // stuff no longer in use
    // console.log("BEFORE", scen)
    const newScen = {
        scenarioType: scenType.name,
        schemaVersion: scenType.schemaVersion
    }
    if (!!scen.id) {
        newScen.id = scen.id
    }
    if (!!scen.name) {
        newScen.name = scen.name
    }
    newScen.info = (!!scen.info) ? scen.info : tmpl.info

    newScen.SAMPLE = (!!scen.SAMPLE) ? scen.SAMPLE : tmpl.SAMPLE
    newScen.OPQRST = (!!scen.OPQRST) ? scen.OPQRST : tmpl.OPQRST
    newScen.vitalSigns = (!!scen.vitalSigns) ? scen.vitalSigns : tmpl.vitalSigns

    const findings = CLMD.filter(rec => REC_TYPES_WITH_FINDINGS.includes(rec.type))
    newScen.assessmentFindings = {}
    if (undefined === scen.assessmentFindings) {
        scen.assessmentFindings = {}
    } else {
        // find deleted findings ids
        Object.keys(scen.assessmentFindings).forEach(findingId => {
            const found = findings.find(rec => rec.id === findingId)
            if (!found) {
                deletions.push(findingId)
            }
        })
    }
    findings.forEach(finding => {
        const val = scen.assessmentFindings[finding.id]
        if (!!val) {
            newScen.assessmentFindings[finding.id] = val
        } else {
            newScen.assessmentFindings[finding.id] =  "Unremarkable"
            const rec = CLMD.find(item => item.id === finding.id)
            additions.push(rec)
        }
    })

    const checkListItemsWithOptions = CLMD.filter(rec => REC_TYPES_WITH_OPTIONS.includes(rec.type))
    newScen.checkListCorrectness = {}
    if (undefined === scen.checkListCorrectness) {
        scen.checkListCorrectness = {}
    } else {
        Object.keys(scen.checkListCorrectness).forEach(itemId => {
            const found = checkListItemsWithOptions.find(rec => rec.id === itemId)
            if (!found) {
                deletions.push(itemId)
            }
        })
    }
    checkListItemsWithOptions.forEach(cli => {
        const val = scen.checkListCorrectness[cli.id]
        if (!!val) {
            newScen.checkListCorrectness[cli.id] = val
        } else {
            newScen.checkListCorrectness[cli.id] = null
            const rec = CLMD.find(item => item.id === cli.id)
            additions.push(rec)
        }

    })

    newScen.correctMedicationDosages = {}
    if (undefined === scen.correctMedicationDosages) {
        // so we don't have to check if scen.correctMedicationDosages is
        // defined in the loop below
        // console.log("scen.correctMedicationDosages doesn't exist")
        scen.correctMedicationDosages = {}
    }
    // create a record for each med in use, filtering out ones which no
    // longer are
    MEDS.forEach(med => {
        let val = scen.correctMedicationDosages[med.id]
        if (undefined !== val && "object" === typeof val) {
            // console.log(val)
            let newVal = {}
            // fill in any missing fields with default values, also
            // correcting old default value of "default" to ""
            for (const [key, defaultValue] of Object.entries(MED_DEFAULTS)) {
                if (!(key in val)) {
                    newVal[key] = defaultValue
                } else {
                    let fldVal = val[key]
                    if ("default" === fldVal) {
                        // console.log(`changing ${med.id}:${key} = "default" to ""`)
                        fldVal = ""
                    }
                    newVal[key] = fldVal
                }
            }
            newScen.correctMedicationDosages[med.id] = newVal
        } else {
            // no record for this med, set to default object
            newScen.correctMedicationDosages[med.id] = MED_DEFAULTS
        }
    })

    newScen.problems = []
    if (!!scen.problems) {
        scen.problems.forEach((prob, i) => {
            const currProb = {...prob}
            currProb.assessments = []
            currProb.solutions = []
            prob.assessments.forEach(assessID => {
                // copy over assessIDs if they still exist, else mark
                // as deleted (they will probably show up as dups, perhaps
                // I should search within deletions for matching ID instead)
                if(CLMD[assessID] === undefined){
                    deletions.push(assessID)
                } else {
                    currProb.assessments.push(assessID)
                }
            })
            prob.solutions.forEach(sol => {
                const newSol = {...sol}
                newSol.actions = []
                sol.actions.forEach(act => {
                    if (INTVS.find(e => e.id === act.id) === undefined) {
                        deletions.push(act.id)
                    } else {
                        newSol.actions.push(act)
                    }
                })
                currProb.solutions.push(newSol)
            })
            // munge prob/solutions/actions if necessary
            newScen.problems.push(currProb)
        })
    }
    // console.log(newScen.problems, scen.problems)
    // } else {
    //     newScen.problems = []
    // }

    newScen.associations = {}
    // if scen has associations, ensure that it's promptID => promptAnswerID || [promptAnswerID, ...]
    // is still valid
    if (!!scen.associations) {
        Object.entries(scen.associations).forEach(([promptID, val]) => {
            if (INTV_PROMPTS.find(e => e.id === promptID) === undefined) {
                deletions.push(promptID)
            } else {
                if (typeof val === "string") {
                    if (INTV_PROMPT_ANSWERS.find(a => a.id === val) === undefined) {
                        deletions.push(val)
                    } else {
                        newScen.associations[promptID] = val
                    }
                } else {
                    const tmp = []
                    val.forEach(paID => {
                        if (INTV_PROMPT_ANSWERS.find(a => a.id === paID) === undefined) {
                            deletions.push(paID)
                        } else {
                            tmp.push(paID)
                        }
                        newScen.associations[promptID] = [...tmp]
                    })
                }
            }
        })
    }
    console.log("additions", additions)
    console.log("deletions", deletions)
    // console.log(newScen.correctMedicationDosages)
    // console.log("AFTER", newScen)
    return newScen
}
