import { useEffect, useRef, useState } from "react"

import classnames from "classnames"

import { useScenarioEditor } from "./ScenarioEditorContext"

const CheckListCorrectness = ({active }) => {
    const {scenario, saveScenario, scenarioType } = useScenarioEditor()
    const [correctAnswers, setCorrectAnswers] = useState(null)
    const [validationErrors, setValidationErrors] = useState(null)
    const [haveErrors, setHaveErrors] = useState(false)
    const formEL = useRef(null)

    useEffect(
        () => {
            if (!!scenario.checkListCorrectness) {
                setCorrectAnswers(scenario.checkListCorrectness)
                const valErrors = {}
                Object.entries(scenario.checkListCorrectness).forEach(([key, value]) => {
                    valErrors[key] = false
                })
                setValidationErrors(valErrors)
            }
        }, [scenario.checkListCorrectness]
    )

    // useEffect(
    //     () => {
    //         if (!!correctAnswers) {
    //             console.log(JSON.stringify(correctAnswers, null, 4))
    //         }
    //     }, [correctAnswers]
    // )

    // useEffect(
    //     () => {
    //         !!validationErrors && console.log(JSON.stringify(validationErrors, null, 4))
    //     }, [validationErrors]
    // )

    const handleCheck = (opt) => {
        // console.log(opt.promptID, opt.id)
        // console.log(`opt.parentID ${opt.parentID} opt.promptID ${opt.promptID}`)
        setCorrectAnswers(prevState => Object.assign({}, prevState, {[opt.parentID]: opt.id}))
        setValidationErrors(prevState => Object.assign({}, prevState, {[opt.parentID]: false}))
    }

    const handleSubmit = async (event) => {
        event.preventDefault()
        const form = formEL.current
        setHaveErrors(false)
        let hasValidationErrors = false
        for (const fld of form.querySelectorAll('input[type=radio]:required')) {
            const isValid = !!correctAnswers[fld.name]
            // console.log(fld.name, isValid)
            setValidationErrors(prevState => Object.assign({}, prevState, { [fld.name]: !isValid }))

            if (!isValid) {
                hasValidationErrors = true
            }
        }
        setHaveErrors(hasValidationErrors)

        if (!hasValidationErrors) {
            // console.log("no errors")
            const newScen = Object.assign({}, scenario)
            Object.entries(correctAnswers).forEach(([key, val]) => {
                newScen.checkListCorrectness[key] = val
            })
            await saveScenario(newScen)
        }
    }

    const renderChoices = (id) => {
        const prompt  = scenarioType.getChecklistItem(id)
        const options = scenarioType._checklist.filter(rec => rec.parentID === id)

        return (
            <>
                <span>
                    <i>{prompt.label}</i> &nbsp; &nbsp;
                    <span className={classnames({ hidden: !haveErrors || !validationErrors[id] })}>
                        Please select the correct answer
                    </span>
            </span>
            {options.map((opt, i) =>
            <div key={opt.id}>
                <label>
                    <input
                        type="radio"
                        name={id}
                        required={i === 0}
                        value={opt.id}
                        checked={correctAnswers[id] === opt.id}
                        onChange={() => handleCheck(opt)}
                    />
                    {opt.label}
                </label>
            </div>
            )}
            <br/>
            </>
        )
    }
    if (!correctAnswers) {
        return (<div>Loading...</div>)
    }
    return (
        <form
            noValidate={true}
            className={classnames("scenario-form", { "scenario-view-hidden": !active })}
            ref={formEL}
        >
            <div
                id="checklist-options"
                className="info-wrapper"
            >
                <button
                    className="save-changes"
                    type="submit"
                    onClick={handleSubmit}
                >
                    Save Changes
                </button>

                {Object.keys(correctAnswers).map(id =>
                    <div key={id}>
                        {renderChoices(id)}
                    </div>
                )}
                <button
                    className="save-changes"
                    type="submit"
                    onClick={handleSubmit}
                >
                    Save Changes
                </button>

            </div>
        </form>
    )
}

export default CheckListCorrectness