import { useState, useEffect, useRef } from 'react';
import Solution from './Solution';
import SearchResults from './SearchResults';
import { matchesVital, matchesChecklist, getLabel } from '../../scripts/id-util';
import { useScenarioEditor } from '../ScenarioEditor/ScenarioEditorContext';

const Problem = ({ problems, number }) => {
    const self = problems[number] ?? {};
    const [render, setRender] = useState(true);
    const [numSolutions, setNumSolutions] = useState(1);
    const addSolution = () => { setNumSolutions(numSolutions + 1); self.solutions.push({}); }

    const deleteSelf = () => {
        setRender(false);
        problems.splice(number, 1);
    };

    const updateSelf = (e) => {
        e.target.type === "text"
            ? self[e.target.name] = e.target.value
            : self[e.target.name] = e.target.checked;
    }

    useEffect(() => {
        setNumSolutions(self?.solutions?.length);
    }, [self.solutions]);

    if (!render) return null;

    if (!self.problem) self.problem = "";
    if (!self.lifeThreat) self.lifeThreat = false;
    // if (!self.protocol) self.protocol = "";

    return (
        <div className="problem-wrapper">
            <div className="layer">
                <div className="problem-meta-info">
                    <p className="problem-heading">Info:</p>
                    <label>
                        Problem:
                        <input type="text"
                            className="mdb-text"
                            name="problem"
                            onChange={updateSelf}
                            defaultValue={self.problem}
                        />
                    </label>
                    {/* <label>
                        Protocol:
                        <input type="text"
                            className="mdb-text"
                            name="protocol"
                            onChange={updateSelf}
                            defaultValue={self.protocol}
                            />
                    </label> */}
                    <label>
                        Life threat?
                        <input type="checkbox"
                            name="lifeThreat"
                            onChange={updateSelf}
                            defaultChecked={self.lifeThreat}
                        />
                    </label>
                </div>
            <button type="button" onClick={deleteSelf}>Delete Problem</button>
            </div>

            <div className="layer">
                <div className="problem-content-block problem-findings">
                    <p className="problem-content-heading">Assessment:</p>
                    <BlockSection type="assessments" self={self} />
                </div>

                <div className="problem-content-block problem-vitals">
                    <p className="problem-content-heading">Vitals:</p>
                    <BlockSection type="vitals" self={self} />
                </div>

                <div className="problem-content-block problem-notes">
                    <p className="problem-content-heading">Notes / Comments:</p>
                    <BlockSection type="notes" self={self} />
                </div>
            </div>

            <div className="layer solutions">
                {[...Array(numSolutions)].map((_, i) => (
                    <Solution key={`solution-${i}`} self={self} number={i} />
                ))}
                <button type="button" onClick={addSolution}>New Solution</button>
            </div>
        </div>
    );
}

const BlockSection = ({ self, type }) => {
    const [num, setNum] = useState(0);
    const more = () => { setNum(num + 1); }

    if (!self[type]) self[type] = [];

    useEffect(() => {
        setNum(self[type].length);
    }, [self, type]);

    return (
        <>
        {[...Array(num)].map( (_, i) => (
            <BlockItem key={`blockItem-${i}`} self={self[type]} num={i} type={type} />
        ))}
        <button type="button" onClick={more} >New</button>
        </>
    );
}

const BlockItem = ({ self, num, type }) => {
    const {scenarioType} = useScenarioEditor()
    if (!self[num] && type !== "vitals")
        self[num] = "";
    else if (!self[num])
        self[num] = { id:"", variant:"" };

    const [render, setRender] = useState(true);
    const [query, setQuery] = useState();
    const inputRef = useRef();

    const deleteSelf = () => {
        self.splice(num, 1);
        setRender(false);
    }

    const updateSelf = (e) => {
        setQuery(e.target.value);
        if (type === "notes") self[num] = e.target.value;
    }

    const setVal = (e) => {
        if (type === "vitals")
            self[num].id = e.target.dataset.id;
        else
            self[num] = e.target.dataset.id;
        inputRef.current.value = e.target.dataset.label;
        setQuery(null);
    }

    if (!render) return null;

    const getValue = (e) => {
        switch (type) {
            case "notes":
                return self[num];
            case "vitals":
                return getLabel(scenarioType, self[num].id, type)?.label;
            default:
                return getLabel(scenarioType, self[num], type)?.label;
        }
    }


    return (
        <label className="mdb-block-item">
            <input
                ref={inputRef}
                onChange={updateSelf}
                defaultValue={getValue()}
                type="text"
                />
        {type === "assessments" && <SearchResults list={matchesChecklist(scenarioType, query)} setVal={setVal} />}
        {type === "vitals" && <><SearchResults list={matchesVital(query)} setVal={setVal} /> <VitalsFields /></>}
        <button type="button" onClick={deleteSelf} >-</button>
        </label>
    );

    function VitalsFields() {
        if (!self[num].variant) self[num].variant = "initial";

        const handleSelect = e => {
            self[num].variant = e.target.value;
        };

        return (
            <select onChange={handleSelect} defaultValue={self[num].variant}>
                <option hidden>Vital Type</option>
                <option value="initial">Initial</option>
                <option value="good">Good</option>
                <option value="bad">Bad</option>
            </select>
        );
    }
}

export default Problem;