import { useRef } from "react";
import classNames from "classnames";

import { useScenarioEditor } from './ScenarioEditorContext';

import { processForm } from './formUtils';

import Vitals from "./Vitals";
import SAMPLE from "./Sample";
import OPQRST from "./OPQRST";

const PatientStatus = ({active}) => {
    const {scenario, saveScenario} = useScenarioEditor()
    const formEL = useRef(null)

    const clickHandler = async (event) => {
        event.preventDefault()
        const form = formEL.current
        const newScen = processForm(scenario, form)
        await saveScenario(newScen)
    }

    return (
        <form className={classNames("scenario-form", {"scenario-view-hidden": !active})} ref={formEL} >
            <button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>

            <Vitals scenario={scenario} />
            <div style={{ display: "flex" }}>
                <div style={{ flex: 1 }}>
                    <SAMPLE scenario={scenario} />
                </div>
                <div style={{ flex: 1 }}>
                    <OPQRST scenario={scenario} />
                </div>
            </div>
            <button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>
        </form>
    )
}

export default PatientStatus