export const immutableAddOrUpdateObjectKey = (prevState, key, value) => {
    return {
        ...prevState,
        [key]: value
    }
}

export const immutableRemoveObjectKey = (prevState, key) => {
    const {
        [key]: removedProperty, ...remainingProperties
    } = prevState
    return remainingProperties
}

export const appendObjToArray = (array, obj) => array.concat(obj)

export const updateObjInArray = (array, obj) => {
    const tmp = [...array]
    const idx = array.findIndex(e => e.id === obj.id)
    tmp[idx] = obj
    return tmp
}

export const removeObjWithIdFromArray = (array, id) => {
    const tmp = [...array]
    const idx = array.findIndex(e => e.id === id)
    tmp.splice(idx, 1)
    return tmp
}

export const swapUp = (array, idx) => {
    const tmp = [...array]
    const removed = tmp.splice(idx, 1)[0]
    tmp.splice(idx - 1, 0, removed)
    return tmp
}

export const swapDown = (array, idx) => {
    const tmp = [...array]
    const removed = tmp.splice(idx, 1)[0]
    tmp.splice(idx + 1, 0, removed)
    return tmp
}
