import { useState } from 'react';
import Actions from './Actions';
import Contraindications from './Contraindications';

const Solution = ({ self: problem, number }) => {
    const [render, setRender] = useState(true);
    const [tab, setTab] = useState("A");

    if (!problem.solutions) problem.solutions = [];
    if (!problem.solutions[number] && render) 
        problem.solutions[number] = { protocols: "", minimalWhy: "", actions: [], contraindications: [] };

    const self = problem.solutions[number];

    const updateSelf = (e) => {
        self[e.target.name] = e.target.value;
    }

    const deleteSolution = () => {
        setRender(false);
        problem.solutions.splice(number, 1);
    }

    if (!render) return null;

    return (
        <div className="solution">
            <div className="solution-header">
                <h2>Solution {number + 1}</h2>

                <label className="protocol-info">
                    Protocols:                
                    <input
                        onChange={updateSelf}
                        name="protocols"
                        defaultValue={self.protocols}
                        type="text" />
                </label>    
                <label className="minimal-why-info">
                    Minimal Why:                
                    <input
                        onChange={updateSelf}
                        defaultValue={self.minimalWhy}
                        name="minimalWhy"
                        type="text" />
                </label>  

                <button type="button" className="remove-solution-btn" onClick={deleteSolution} >Delete Solution</button>
            </div>

            <div className="solution-content">
                <div className="solution-tab-selector">
                    <span className="solution-tab" onClick={() => setTab("A")}>Actions</span>
                    <span className="solution-tab" onClick={() => setTab("C")}>Contraindications</span>
                </div>

                <div className="content-wrapper">
                    <Actions display={tab === "A"} self={self} />
                    <Contraindications display={tab === "C"} self={self} />
                </div>
            </div>
        </div>
    );
}

export default Solution;