import {
    // useState,
    // useEffect,
    useRef
} from 'react';

import { useScenarioEditor } from '../ScenarioEditor/ScenarioEditorContext';

const NONE_FIELD = "( None )";

const ActionAssociations = () => {
    const { scenario: mdbForm, scenarioType, saveScenario } = useScenarioEditor();

    if (!mdbForm.associations)
        mdbForm.associations = {};

    const associationsRef = useRef();

    const getUniqueIntvsWithContext = (problems) => {
        // console.log(problems)
        const intvsWithContext = {} //mapping of intvIDs to contexts (str) of where they are used (problem label + solution #)
        problems.forEach(problem => {
            // console.log(problem)
            const currProbLabel = problem.problem;
            // console.log(currProbLabel)
            problem.solutions.forEach((sol, i) => {
                // console.log(sol, i)
                sol.actions.filter(act => "id" in act && act.id !== "" && act.id !== NONE_FIELD)
                            .forEach(act => {
                                const intv = scenarioType.getIntervention(act.id)
                                if (!(intv.id in intvsWithContext)) {
                                    intvsWithContext[intv.id] = {
                                        id: intv.id,
                                        label: intv.label,
                                        contexts: new Set(),
                                        prompts: intv.prompts
                                    }
                                }
                                intvsWithContext[intv.id].contexts.add(`Problem: ${currProbLabel} Solution: ${i+1}`)
                            })
            })
        })
        const retVal = Object.values(intvsWithContext).map(ctx => {
            ctx.contexts = Array.from(ctx.contexts)
            return ctx
        }).filter(e => e.prompts.length > 0)
        return retVal
    }

    // Set list of prompts
    // useEffect(() => {
    //     const tmp = getUniqueIntvsWithContext(mdbForm.problems)
    //     const tmp2 = getIntvPrompts(tmp)
    //     setIntvs(tmp2);
    // }, [getIntvPrompts, mdbForm.problems]);

    const handleSubmit = async () => {
        let obj = {};
        for (let item of associationsRef.current)
        {
            if (item.tagName === 'SELECT')
            {
                obj[item.name] = item.value;
            }
            else
            {
                if (!obj[item.name])
                    obj[item.name] = [];

                if (item.checked)
                    obj[item.name].push(item.value);
            }
        }

        mdbForm.associations = obj;
        await saveScenario(mdbForm);
    }

    const getSelected = (parent) => {
        return mdbForm.associations
            ? mdbForm.associations[parent]
            : null;
    }

    const isChecked = (parent, item) => {
        return mdbForm.associations[parent]
            ? mdbForm.associations[parent].includes(item)
            : false;
    }

    const getOptionLabel = (option) => {
        return (!!option.abbrLabel) ? option.abbrLabel : option.label
    }

    const renderPrompt = (promptID) => {
        const {answers, ...prompt} =  scenarioType.getPrompt(promptID, true)
        prompt.options = answers
        if (prompt.selectionType === "one") {
            return (
                <label className="associations-or">
                    <i>{prompt.label}</i>
                    <select
                        name={prompt.id}
                        defaultValue={getSelected(prompt.id)}
                    >
                        <option value=""></option>
                        {prompt.options?.map(opt => (
                            <option
                                key={opt.id}
                                value={opt.id}
                            >
                                {getOptionLabel(opt)}
                            </option>
                        ))}
                    </select>
                </label>
            )
        } else {
            return (
                <label className="associations-and">
                    <i>{prompt.label}</i>
                    {prompt.options?.map(child => (
                        <label key={child.id}>
                            <input
                                type="checkbox"
                                value={child.id}
                                name={prompt.id}
                                defaultChecked={isChecked(prompt.id, child.id)}
                            />
                            {getOptionLabel(child)}
                        </label>
                    ))}
                </label>
            )
        }
    }

    // if (!(intvs.length > 0)) {
    //     return (<p>Nothing to associate answers with.</p>)
    // }

    return (
        <div id="associations-wrapper">
            <h1 id="title">Correct Prompt Answers</h1>
            <form id="associations-list" ref={associationsRef}>
                {getUniqueIntvsWithContext(mdbForm.problems).map(intvContext =>
                    <div key={intvContext.id}>
                        <span>
                            <b style={{ fontWeight: "bold" }}>Intervention:</b> &nbsp; {intvContext.label} &nbsp;
                            <b style={{ fontWeight: "bold" }}>Contexts:</b> {intvContext.contexts.join(", ")}
                        </span>
                        <br/>
                        {intvContext.prompts.map(promptID =>
                            <div key={promptID}>
                                {renderPrompt(promptID)}
                            </div>
                        )}
                    </div>
                )}
            </form>
            <button
                type="button"
                onClick={handleSubmit}
                id="associations-submit"
            >
             Save Changes
            </button>
        </div>
    )
}



export default ActionAssociations;