import { useEffect, useState } from "react"


import { db } from "@meddbriefer/mdb-firebase"
import { getScenarioType } from "@meddbriefer/scenario-data/index.js";

import { updateSchema } from "./traumaScenario"
import blankTrauma from '../../data/blank-trauma.json';

import SetScenarioNameForm from "./SetScenarioNameForm";

const CloneScenario = (props) => {
    const {scenarioId} = props.match.params
    const [scenario, setScenario] = useState(null)

    useEffect(() => {
        (async () => {
            const doc = await db.collection("scenarios").doc(scenarioId).get()
            const scenData = doc.data()
            const scenTypeData = await getScenarioType(db, scenData.scenarioType, scenData.schemaVersion)
            delete scenData.id
            delete scenData.name
            setScenario(updateSchema(scenData, scenTypeData, blankTrauma))
        })()
    }, [scenarioId])

    return (
        <div>

        {!!scenario
            ? <SetScenarioNameForm scenario={scenario} isTemplate={true} />
            : <div>loading...</div>
        }
        </div>
    )
}

export default CloneScenario