
const VITALS = ["BP", "P", "R", "Skin", "Spo2", "ETCO2", "GCS", "Glucose", "Pain", "Temp"]
const TYPES = ["initial", "good", "bad"]

const Vitals = ({scenario}) => {

	return (
		<div className="info-box vitals">
			<h1>Vital Signs</h1>
			<table>
				<thead>
					<tr>
						<th>Vital</th>
						<th>Initial Value</th>
						<th>Good Reassessment Value</th>
						<th>Bad Reassessment Value</th>
					</tr>
				</thead>
				<tbody>
					{VITALS.map(vital =>
						<tr key={`${vital}-row`}>
							<th>{vital}</th>
							{TYPES.map(type =>
								<td key={`${vital}-${type}-cell`}>
									<input
										id={`vitalSigns-${vital}-${type}`}
										type="text"
										defaultValue={scenario.vitalSigns[vital][type]}
									/>
								</td>
							)}
						</tr>
					)}
				</tbody>
			</table>
		</div>
	)
}


export default Vitals;
