import { useRef } from 'react';

import classnames from 'classnames';

import { db } from '@meddbriefer/mdb-firebase';
import { verifyDocNameUniqueness } from "@meddbriefer/scenario-data"

import { useScenarioEditor } from './ScenarioEditorContext';

import { processForm } from './formUtils';


const BasicInfo = ({ active }) => {
	const {scenario, saveScenario } = useScenarioEditor()
	const formEL = useRef(null)
	const info = scenario.info;
  	const pt = 'patientInformation';

	const clickHandler = async (event) => {
		event.preventDefault()
		const form = formEL.current
		const newScen = processForm(scenario, form)
		// before saving, verify that if they changed the scenario name, it is still unique
		const newNameIsUnique = await verifyDocNameUniqueness(db, "scenarios", newScen.name, "name", "V2")
		if (scenario.name === newScen.name) {
			await saveScenario(newScen)
		} else if (!newNameIsUnique) {
			alert("Please give this scenario a unique name")
		} else {
			await saveScenario(newScen)
		}

	}

	return (
		<form className={classnames("scenario-form", { "scenario-view-hidden": !active })} ref={formEL} >
			<button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>
			<div id="basic-info" className="info-wrapper">
				<h1>Basic Information:</h1>
				<label>
					Scenario Name:
					<input id="name" type="text" defaultValue={scenario.name} />
				</label>
				<label>
					Instructor Info:
					<textarea id="info-instructorInformation" defaultValue={info.instructorInformation} />
				</label>
				<label>
					Dispatch Info:<textarea id="info-dispatchInfo" defaultValue={info.dispatchInfo} />
				</label>
				<label>
					Scene assessment:<textarea id="info-sceneAssessment" defaultValue={info.sceneAssessment} />
				</label>
				<label>
					Maximum time for scenario:<input id="info-maxTime" type="number" placeholder="minutes" defaultValue={info.maxTime} />
				</label>
				<h1>Patient Info:</h1>
				<div id="pt-info">
					<label>
						Moulage:<input type="text" id={`info-${pt}-moulage`} defaultValue={info.patientInformation.moulage} />
					</label>
					<label>
						Position:<input type="text" id={`info-${pt}-position`} defaultValue={info.patientInformation.position} />
					</label>
					<label>
						Actions:<input type="text" id={`info-${pt}-actions`} defaultValue={info.patientInformation.actions} />
					</label>
					<label>
						Age:<input type="number" id={`info-${pt}-age`} defaultValue={info.patientInformation.age} />
					</label>
					<label>
						Weight (kg):<input type="text" id={`info-${pt}-weight`} defaultValue={info.patientInformation.weight} />
					</label>
					<label>
						Gender:<input type="text" id={`info-${pt}-gender`} defaultValue={info.patientInformation.gender} />
					</label>
				</div>
			</div>
			<button className="save-changes" type="submit" onClick={clickHandler }>Save Changes</button>
		</form>
	);
};

export default BasicInfo;
