import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import classnames from 'classnames';

import { db } from "@meddbriefer/mdb-firebase";

import {
	getScenarioTypeObject,
	getDocument,
	saveDocument
} from "@meddbriefer/scenario-data/index.js";

import blankTrauma from '../../data/blank-trauma.json';
import { updateSchema } from "./traumaScenario.js";

import { ScenarioEditorContext } from "./ScenarioEditorContext";
import { useHeader } from "../HeaderContext.js";

import BasicInfo from "./BasicInfo.js";
import PatientStatus from "./PatientStatus.js";
import Steplist from "./Steplist.js";
import CheckListCorrectness from "./CheckListCorrectness.js";
import Medications from "./Medications.js";
import InterventionAnswers from "./InterventionAnswers.js";
import Problems from "./Problems.js";

const COLLECTION_ID = 'scenarios';

const EditScenario = () => {
	const { scenarioId } = useParams()
	const {setTitle} = useHeader()
	const [scenario, setScenario] = useState(null);
	const [scenarioTypeData, setScenarioTypeData] = useState(null)
	const [activeTab, setActiveTab] = useState('basic-info');

	const isActiveTab = (name) => name === activeTab;

	window.scenario = scenario
	// let history = useHistory();

	const saveScenario = async (scenData) => {
		// strips fields whose values are "undefined". there *must* be a better way
		// I imagine better field validation is in order
		const data = JSON.parse(JSON.stringify(scenData))
		setScenario(data)
		await saveDocument(db, "scenarios", data)
		alert("changes saved")
	}

	// Load and apply selected scenario
	useEffect(() => {
		(async () => {
			const scenData = await getDocument(db, COLLECTION_ID, scenarioId)
			setTitle(`Scenario: ${scenData.name}`)
			const scenTypeData = await getScenarioTypeObject(db, scenData.scenarioType, scenData.schemaVersion)
			setScenarioTypeData(scenTypeData)
			// make use of scenTypeData and other sources to ensure data from db is in sync
			// with latest schema
			const scen = updateSchema(scenData, scenTypeData, blankTrauma)
			setScenario(scen)
		})()
	}, [scenarioId, setTitle]);

	if (!scenario || !scenarioTypeData) {
		return (<div>Loading...</div>)
	}
	return (
		 <ScenarioEditorContext.Provider value={{
		 	scenario,
		 	saveScenario,
		 	scenarioType: scenarioTypeData
		  }}
		 >
			<nav id="tab-selector">
				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("basic-info") })}
					onClick={() => setActiveTab('basic-info')}
				>
					Info
				</div>
				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("patient-status") })}
					onClick={() => setActiveTab('patient-status')}
				>
					Patient Status
				</div>

				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("steplist") })}
					onClick={() => setActiveTab('steplist')}
				>
					Assessment Findings
				</div>
				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("checklist-correctness") })}
					onClick={() => setActiveTab('checklist-correctness')}
				>
					Checklist Correctness
				</div>

				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("medications") })}
					onClick={() => setActiveTab('medications')}
				>
					Medications
				</div>
				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("problems") })}
					onClick={() => setActiveTab('problems')}
				>
					Problems
				</div>
				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("intv-answers") })}
					onClick={() => setActiveTab("intv-answers")}
				>
					Intervention Answers
				</div>
			</nav>

			<div className="scenario-form-wrapper">
				{
					<>
						<BasicInfo active={isActiveTab('basic-info')} />
						<PatientStatus active={isActiveTab('patient-status')} />
						<Steplist active={isActiveTab('steplist')} scenario={scenario} />
						<CheckListCorrectness active={isActiveTab('checklist-correctness')} scenario={scenario} />
						<Medications active={isActiveTab('medications')} scenario={scenario} />
						<Problems active={isActiveTab('problems')} scenario={scenario} />
						<InterventionAnswers active={isActiveTab("intv-answers")} scenario={scenario} />
					</>
				}
			</div>
		</ScenarioEditorContext.Provider>
	)

}




export default EditScenario
