import { MDB_TYPES } from "@meddbriefer/scenario-data/constants.js";
const ScenarioItem = ({item, scenario}) => {
  return (
    item.type === MDB_TYPES.PHASE || item.type === MDB_TYPES.SUB_PHASE
    ? <ScenarioHeader item={item} />
    : <div className="step-item"><Assessment item={item} scenario={scenario} /></div>
  );
};

const ScenarioHeader = ({item}) => {
  return <div className="list-item header-item"><h2>{item.label}</h2></div>;
};

const Assessment = ({item, scenario}) => {
	const findings = scenario.assessmentFindings
  return (
    <label>
      {`'${item.label}':`}
      <input
        type="text"
        id={`assessmentFindings-${item.id}`}
        placeholder="leave blank if N/A or unremarkable"
        defaultValue={findings[item.id]}
      />
    </label>
  );
};

export default ScenarioItem;
