import { useEffect, useRef, useState } from 'react';

import classnames from 'classnames';

import { MDB_TYPES as TYPES } from "@meddbriefer/scenario-data/constants.js"

import { useScenarioEditor } from './ScenarioEditorContext';

import { processForm } from './formUtils';

import ScenarioItem from "./ScenarioItem.js";

const UNWANTED_CHECKLIST_TYPES = [TYPES.AWO, TYPES.CL_PROMPT, TYPES.DECISION, TYPES.DECISION_OPTION, TYPES.REQURED]


// only handles a simple map of keys to scalar types
// const compareObjs = (objA, objB) => {
// 	Object.keys(objA).forEach(key => {
// 		if (objA[key] !== objB[key]) {
// 			console.log(key, `BEFORE: ${objA[key]}`, `AFTER: ${objB[key]}`)
// 		}
// 	})
// }

const Steplist = ({ active }) => {
	const formEL = useRef(null)
	const {scenario, saveScenario, scenarioType} = useScenarioEditor()
	const [steps, setSteps] = useState([])

	const clickHandler = async (event) => {
		event.preventDefault()
		const form = formEL.current
		// console.log("BEFORE", scenario.id, scenario.assessmentFindings)
		const newScen = processForm(scenario, form)
		// console.log("AFTER", newScen.id, newScen.assessmentFindings)
		// compareObjs(scenario.assessmentFindings, newScen.assessmentFindings)
		await saveScenario(newScen)
	}

	useEffect(
		() => {
			if (!!scenarioType) {
				// filter out checklist items, other than PHASEs and SUB_PHASEs
				// (for headings) which don't have findings
				const tmp = scenarioType._checklist
								.filter(rec => !UNWANTED_CHECKLIST_TYPES.includes(rec.type))
				setSteps(tmp)
			}
		}, [scenarioType]
	)

	if (!steps) {
		return (<div>Loading...</div>)
	}
	return (
		<form className={classnames("scenario-form", { "scenario-view-hidden": !active })} ref={formEL}>
			<button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>

			<div id="scenario-steps">
				<h1>Scenario Items</h1>
				{steps.map((item) => (
					<ScenarioItem key={item.id} item={item} scenario={scenario} />
				))}
			</div>
			<button className="save-changes" type="submit" onClick={clickHandler}>Save Changes</button>

		</form>
	);
};

export default Steplist;
