const SearchResults = ({ list, setVal }) => {
    if (!list) return null;

	const none = { id: "( None )", label: "( None )" };

    return (
		<div className="search-results">
			{[none, ...list].map((e, i) => (
				<p
					key={i}
					data-label={e.label}
					data-id={e.id}
					onClick={(e) => setVal(e)}
				>
					{e.label} <b>{e.type ? `( ${e.type} )` : ""}</b>
				</p>
			))}
		</div>
    );
}

export default SearchResults;